import * as types from "../mutation-types";
import router from "@/router";
const querystring = require("query-string");
import api from "@/api";

/**
 * @module store/modules/institutions
 * @desc Magazyn do przechowywania informacji dotyczcych instytucji
 */

/** obiekt przechowujcy aktualne stany danych zmiennych pobranych z API
 *  @name institutions/state
 * @type {Object}
 * @property {Array} institutionsList- tablica przechowujca list obiektw instytucji pobranych z API
 */
const state = {
  institutionsList: []
};

/** obiekt posiadajcy funkcje ktre zwracaj obiekty lub tablice z obiektu state
 *  @name institutions/getters
 * @type {Object}
 * @getter institutionsList - funkcja zwracajca list instytucji
 */
const getters = {
  institutionsList: state => state.institutionsList
};

/** obiekt posiadajcy funkcje, ktre wywouj funkcje pobierajce dane z API, a anstpnie zapisuj te dane poprzez mutacje do obiektw w state
 *  @name institutions/actions
 * @desc
 * getInstitutions - funkcja pobiera list instytucji z API <br/>
 * addInstitutions - wywouje zapytanie aby doda now instytucj <br/>
 * deleteInstitution - wywouje zapytanie w celu usunicia instytucji <br/>
 * editInstitution - wywouje zpytanie w celu edycji danych instytucji <br/>
 */
const actions = {
  getInstitutions({ commit, state }, data) {
    return new Promise((resolve, reject) => {
      api.getInstitutions(response => {
        commit("getInstitutions", response);
        resolve();
      });
    });
  },
  addInstitutions({ dispatch }, data) {
    api.addInstitutions(response => {
      this.dispatch("toastMessage/alert", {
        message: "Placwka zostaa dodana",
        type: "success"
      });
      dispatch("getInstitutions");
    }, data);
  },
  deleteInstitution({ commit }, data) {
    api.deleteInstitutions(response => {
      this.dispatch("toastMessage/alert", {
        message: "Placwka zostaa usunita",
        type: "info"
      });
      commit("deleteInstitution", data);
    }, data);
  },
  editInstitution({ dispatch }, data) {
    api.editInstitutions(response => {
      this.dispatch("toastMessage/alert", {
        message: "Dane placwki zostay zmienione",
        type: "success"
      });
      dispatch("getInstitutions");
    }, data);
  }
};
/** obiekt posiadajcy funkcje ktre zmieniaj stan zmiennych w obiekcie state
 * @name institutions/mutations
 * @type {object}
 * @mutator  getInstitutions - funkcja zapisujaca list instytucji w obiekcie state.institutionsList
 * @mutator deleteInstitution - funkcja uaktualniajca list instytucji w obiekcie state
 */
const mutations = {
  getInstitutions(state, data) {
    state.institutionsList = data;
  },
  deleteInstitution(state, data) {
    let removeIndex = state.institutionsList
      .map(item => item.publicId)
      .indexOf(data);
    state.institutionsList.splice(removeIndex, 1);
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
