import * as types from "../mutation-types";
import router from "@/router";
const querystring = require("query-string");
import api from "@/api";

/**
 * @module store/modules/user
 * @desc Magazyn do przechowywania informacji o zalogowanym uytkowniku
 */

/** obiekt przechowujcy aktualne stany danych zmiennych pobranych z API
 *  @name user/state
 * @type {Object}
 * @property {String} id - id uytkownika
 * @property {Boolean} logged_in - true jeeli uytkownik jest zalogowany
 * @property {Boolean} getToken - true jeeli token zosta zapisany
 * @property {String} expires - czas wyganicia tokenu
 * @property {String} token - token uytkownika zwracany po zalogowaniu
 * @property {String} refToken - refresh token - suy do odwieania czasu tokena
 * @property {Object} userInfo - obiekt zawierajcy informacje o danym uytkowniku
 * @property {Array} myPermissions - tablica zawierajca informacje o uprawnieniach danego uytkownika
 */
const state = {
  id: "",
  logged_in: false,
  getToken: false,
  token: String,
  refToken: String,
  expires: String,
  userInfo: false,
  myPermissions: []
};

/** obiekt posiadajcy funkcje ktre zwracaj obiekty lub tablice z obiektu state
 *  @name user/getters
 * @type {object}
 * @getter userId - funkcja zwracajca id uytkownika
 * @getter getToken - funkcja zwracajca token
 * @getter myPermissions - funkcja zwracajca dostpne uprawnienia uytkownika
 * @getter userInfo - funkcja zwraca informacj o uytkowniku
 * @getter loggedIn - funkcja zwraca czy uytkownik jest zalogowany
 * @getter isExpired - funkcja zwraca czy uytkownik token jest jeszcze aktywny
 */
const getters = {
  userId: state => state.id,
  getToken: state => {
    //data
    state.getToken;
  },
  myPermissions: state => state.myPermissions,
  userInfo: state => state.userInfo,
  loggedIn: state => state.logged_in,
  isExpired: state => new Date(state.expires) < new Date()
};

/** obiekt posiadajcy funkcje, ktre wywouj funkcje pobierajce dane z API, a anstpnie zapisuj te dane poprzez mutacje do obiektw w state
 *  @name user/actions
 * @desc
 *  login - funkcja zajmujca sielogowaniem uytkownica do systemu <br/>
 *  getUserData - funkcja pobiera dane dotyczce zalogowanego uzytkownika <br/>
 *  refreshToken - funkcja odwieajca token <br/>
 * logout - funkcja suca do wylgowania uytkownika <br/>
 * register - funkcja suca do rejestracji nowego uzytkownika
 */
const actions = {
  login({ commit, state }, data) {
    api.login(response => {
      commit("saveToken", response);
      commit(types.GET_TOKEN, {});
      api.getPermission(response => {
        commit("myPermissions", response);
      });
      this.dispatch("user/getUserData");
    }, data);
  },
  getUserData({ commit }) {
    api.getUserData(response => {
      commit("saveUserInfo", response);
      router.push({ path: "menu" });

      this.dispatch("toastMessage/alert", {
        message: "Zostae poprawnie zalogowany",
        type: "success"
      });
    });
  },
  refreshToken({ state, commit }) {
    api.refreshToken(
      response => {
        commit("saveToken", response);
        commit(types.GET_TOKEN, {});
      },
      {
        token: state.token,
        ref: state.refToken,
        exp: state.expires
      }
    );
  },
  logout({ commit }) {
    commit(types.LOGOUT_USER);
    router.push({ path: "login" });
  },
  register({ commit, state }, data) {
    api.register(response => {
      router.push({ path: "login" });
      this.dispatch("toastMessage/alert", {
        message: "Zostae poprawnie zarejestrowany",
        type: "success"
      });
    }, data);
  }
};
/** obiekt posiadajcy funkcje ktre zmieniaj stan zmiennych w obiekcie state
 * @name user/mutations
 * @type {object}
 * @mutator myPermissions - funkcja zapisujaca list uprawnien uytkownika w obiekcie state
 * @mutator saveUserInfo - funkcja zapisjca aktualne informacje o zalogowanym uzytkowniku w obiekcie state
 * @mutator saveToken - funkcja zapisujca token w obiekcie state
 */
const mutations = {
  myPermissions(state, data) {
    state.myPermissions = data;
  },
  saveUserInfo(state, userInfo) {
    state.userInfo = userInfo;
  },
  saveToken(state, data) {
    state.logged_in = true;
    state.token = data.token;
    state.refToken = data.refresh;
    state.expires = data.expires;
  },

  [types.LOGOUT_USER](state) {
    state.logged_in = false;
    state.userInfo = false;
    state.myPermissions = [];
    state.token = "";
    state.refToken = "";
    state.expires = "";
  },
  [types.GET_TOKEN](state) {
    state.getToken = true;
  },
  [types.GET_TOKEN_AF](state) {
    state.getToken = false;
  },
  [types.SAVE_TOKEN](state, { token }) {
    state.token = token;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
