import * as types from "../mutation-types";
import router from "@/router";
import axios from "axios";
const querystring = require("query-string");
import api from "@/api";

/**
 * @module store/modules/admin
 * @desc Magazyn do przechowywania informacji do ktrych dostp ma administartor
 */

/** obiekt przechowujcy aktualne stany danych zmiennych pobranych z API
 *  @name admin/state
 * @type {Object}
 * @property {Array} userList - tablica przechowujca list obiektw uytkownikw pobranych z API
 * @property {Object} permissions - obiekt zawierajcy informacje o dostpnych uprawnieniach
 * @property {Object} currentUserPermissions - obiekt zawierajcy informacje o uprawnieniach danego uytkownika
 */
const state = {
  userList: [],
  permissions: {},
  currentUserPermissions: {}
};

/** obiekt posiadajcy funkcje ktre zwracaj obiekty lub tablice z obiektu state
 *  @name admin/getters
 * @type {object}
 * @getter userList - funkcja zwracajca list uytkownikw
 * @getter currentUserPermissions - funkcja zwracajca aktualne uprawnienia danego uytkownika
 * @getter permissions - funkcja zwracajca dostpne uprawnienia uytkownikw
 * @getter setUserPermissions - funkcja wywouje funkcj ktra wysyla zapytanie do API o zmian uprwnie uytkownika
 */
const getters = {
  userList: state => state.userList,
  currentUserPermissions: state => state.currentUserPermissions,
  permissions: state => state.permissions
};

/** obiekt posiadajcy funkcje, ktre wywouj funkcje pobierajce dane z API, a anstpnie zapisuj te dane poprzez mutacje do obiektw w state
 *  @name admin/actions
 * @desc
 *  getPermissions - funkcja pobiera dostpne uprawnienia z API nstpnie wywouje mutacje "permissions" <br/>
 *  getUsers - funkcja pobiera wszystkich uytkownikw z API, nastpnie wywouje mutacj "users" <br/>
 *  getUserPermissions - funkcja pobierajca z API uprawnienia danego uzytkownika <br/>
 */
const actions = {
  getPermissions({ commit }) {
    api.getPermissions(response => {
      commit("permissions", response);
    });
  },
  getUsers({ commit }, data) {
    return new Promise((resolve, reject) => {
      api.getUsers(response => {
        commit("users", response);
        resolve();
      }, data);
    });
  },
  getUserPermissions({ commit }, data) {
    return new Promise((resolve, reject) => {
      api.getUserPermissions(
        response => {
          commit("userPermissions", response);
          resolve(response);
        },
        { id: data }
      );
    });
  },
  setUserPermissions({ commit }, data) {
    return new Promise((resolve, reject) => {
      api.setUserPermissions(response => {
        this.dispatch("toastMessage/alert", {
          message: "Uprawnienia zostay zmienione",
          type: "success"
        });
        resolve();
      }, data);
    });
  }
};
/** obiekt posiadajcy funkcje ktre zmieniaj stan zmiennych w obiekcie state
 * @name admin/mutations
 * @type {object}
 * @mutator users - funkcja zapisujaca list uytkownikw w obiekcie state.userList
 * @mutator userPermissions - funkcja zapisjca aktualne uprawnienia danego uytkownika w obiekcie state.currentUserPermissions
 * @mutator permissions - funkcja zapisujca uprawnienia uytkownika w obiekcie state.permissions
 */
const mutations = {
  permissions(state, data) {
    state.permissions = data;
  },
  users(state, data) {
    state.userList = data;
  },
  userPermissions(state, data) {
    state.currentUserPermissions = data;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
