import * as types from "../mutation-types";
import router from "@/router";
import axios from "axios";
const querystring = require("query-string");
import api from "@/api";

/**
 * @module store/modules/orders
 * @desc Magazyn do przechowywania informacji o zamwieniach
 */

/** obiekt przechowujcy aktualne stany danych zmiennych pobranych z API
 *  @name orders/state
 * @type {Object}
 * @property {Array} ordersList - tablica zawierajca zamwienia
 * @property {Array} summary - tablica zawierajca statystyki oglne
 * @property {Array} summaryInstitution - tablica zawierajca statystyki konkretnej instytucji
 */
const state = {
  ordersList: [],
  summary: [],
  summaryInstitution: []
};

/** obiekt posiadajcy funkcje ktre zwracaj obiekty lub tablice z obiektu state
 *  @name orders/getters
 * @type {object}
 * @getter ordersList - funkcja zwracajca list zamwie
 * @getter ordersList - funkcja zwracajca statystyki oglne
 * @getter ordersList - funkcja zwracajca statystyki konkretnej instytucji
 */
const getters = {
  ordersList: state => state.ordersList,
  summary: state => state.summary,
  summaryInstitution: state => state.summaryInstitution
};

/** obiekt posiadajcy funkcje, ktre wywouj funkcje pobierajce dane z API, a anstpnie zapisuj te dane poprzez mutacje do obiektw w state
 *  @name orders/actions
 * @desc
 *  addOrder - funkcja zapisujca zamwienie w bazie </br>
 *  getOrder - funkcja pobiera zamwienie </br>
 *  getSummary - funkcja pobiera statystyki oglne</br>
 *  getSummaryInstitution - funkcja pobiera statystyki konkretnej instytucji
 */
const actions = {
  addOrder({ commit, dispatch, state }, data) {
    api.addOrder(response => {
      this.dispatch("toastMessage/alert", {
        message: "Zamwienie zoone!",
        type: "success"
      });
    }, data);
  },
  deleteOrders({ commit, dispatch, state }, data) {
    return new Promise((resolve, reject) => {
      let date = new Date();
      let dzien = date.getDay();
      let days = [
        "Niedziela",
        "Poniedziaek",
        "Wtorek",
        "roda",
        "Czwartek",
        "Pitek",
        "Sobota"
      ];
      if (days[dzien] !== data.day) {
        api.addOrder(response => {
          this.dispatch("toastMessage/alert", {
            message: "zamwienie zostao usunite",
            type: "info"
          });
          resolve();
        }, data);
      } else {
        this.dispatch("toastMessage/alert", {
          message: "Nie mona odwoa zamwienia w tym dniu",
          type: "error"
        });
      }
    });
  },
  getOrder({ commit, dispatch, state }, data) {
    return new Promise((resolve, reject) => {
      api.getOrder(response => {
        resolve(response);
      }, data);
    });
  },
  getOrderKids({ commit, dispatch, state }, data) {
    return new Promise((resolve, reject) => {
      api.getOrder(response => {
        resolve(response);
      }, data);
    });
  },
  getSummary({ commit }) {
    return new Promise((resolve, reject) => {
      api.getSummary(response => {
        commit("summary", response);
      });
    });
  },
  getSummaryInstitution({ commit }, data) {
    return new Promise((resolve, reject) => {
      api.getSummaryInstitution(response => {
        commit("summaryInstitution", response);
        resolve(response);
      }, data);
    });
  }
};

/** obiekt posiadajcy funkcje ktre zmieniaj stan zmiennych w obiekcie state
 * @name orders/mutations
 * @type {object}
 * @mutator orders - funkcja zapisujaca list zamwie w obiekcie state
 * @mutator orders - funkcja zapisujaca statystyki oglne w obiekcie state
 * @mutator orders - funkcja zapisujaca statystyki konkretnej instytucji w obiekcie state
 */
const mutations = {
  orders(state, data) {
    state.ordersList = data;
  },
  summaryInstitution(state, data) {
    state.summaryInstitution = data;
  },
  summary(state, data) {
    state.summary = data;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
