import * as types from "../mutation-types";
import router from "@/router";
import axios from "axios";
const querystring = require("query-string");
import api from "@/api";

/**
 * @module store/modules/menu
 * @desc Magazyn do przechowywania informacji o aktualnym menu
 */

/** obiekt przechowujcy aktualne stany danych zmiennych pobranych z API
 *  @name menu/state
 * @type {Object}
 * @property {Array} offersList - tablica zawierajca obiekty aktualnych pozycji w menu
 */
const state = {
  offersList: []
};

/** obiekt posiadajcy funkcje ktre zwracaj obiekty lub tablice z obiektu state
 *  @name menu/getters
 * @type {object}
 * @getter offersList - funkcja zwracajca list menu
 */
const getters = {
  offersList: state => state.offersList
};

/** obiekt posiadajcy funkcje, ktre wywouj funkcje pobierajce dane z API, a anstpnie zapisuj te dane poprzez mutacje do obiektw w state
 *  @name menu/actions
 * @desc
 *  getOffers - funkcja zwracajca list ofert <br/>
 *  addOffer - funkcja dodajca ofert do menu <br/>
 *  deleteOffer - funkcja usuwajca wybran ofert <br/>
 *  updateOffer - funkcja aktualizujca wybran ofert
 */
const actions = {
  getOffers({ commit }) {
    return new Promise((resolve, reject) => {
      api.getOffers(response => {
        commit("offers", response);
        resolve();
      });
    });
  },
  addOffer({ commit, dispatch, state }, data) {
    api.addOffer(response => {
      dispatch("getOffers");
      this.dispatch("toastMessage/alert", {
        message: "Rekord dodany!",
        type: "success"
      });
    }, data);
  },
  deleteOffer({ commit, dispatch, state }, data) {
    api.deleteOffer(response => {
      dispatch("getOffers");
      this.dispatch("toastMessage/alert", {
        message: "Rekord usunity!",
        type: "success"
      });
    }, data);
  },
  updateOffer({ commit, dispatch, state }, data) {
    api.updateOffer(response => {
      dispatch("getOffers");
      this.dispatch("toastMessage/alert", {
        message: "Rekord zaktualizowany!",
        type: "success"
      });
    }, data);
  }
};
/** obiekt posiadajcy funkcje ktre zmieniaj stan zmiennych w obiekcie state
 * @name menu/mutations
 * @type {object}
 * @mutator offers - funkcja zapisujaca list ofert menu w obiekcie state
 */
const mutations = {
  offers(state, data) {
    state.offersList = data;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
